.gallery {
  background-color: #ffffff;

  img {
    width: 100%;
    height: 100%;
  }
}

.gallery__title {
  @include container();
  padding: 48px 0 50px;
  line-height: 2.14;
  text-align: center;
  letter-spacing: 0.4px;
}

.gallery__title-text {
  display: block;
}

.gallery__post-daily,
.gallery__post {
  margin-bottom: 20px;
}

.gallery__photo-daily {
  position: relative;
  margin-bottom: -6px;

  &::before {
    content: "Панорама дня";
    position: absolute;
    top: 0;
    right: 6%;
    width: 140px;
    height: 36px;
    font-size: 12px;
    font-weight: bold;
    line-height: 36px;
    text-transform: uppercase;
    text-align: center;
    vertical-align: middle;
    color: #ffffff;
    background-color: #d22856;
  }
}

.gallery__post-wrapper {
  @include container();
  display: flex;
  flex-direction: column;
}

.gallery__photo {
  margin-bottom: -6px;
}

.gallery__post-desc {
  padding: 18px 19px 19px;
  display: flex;
  flex-direction: column;
  letter-spacing: 0.2px;
  background-color: #f2f2f2;

  &--daily {
    @include container();
    padding: 18px 19px 20px;
  }
}

.gallery__post-title {
  margin-bottom: 2px;
}

.gallery__post-author {
  font-weight: bold;
  font-style: normal;
}

.gallery__post-date {
  font-size: 12px;
  font-weight: lighter;
  text-transform: uppercase;
}

.gallery__post-text {
  margin: 0;
  margin-bottom: 12px;
}

.gallery__post-likes {
  display: flex;
  align-items: center;
}

.gallery__post-like {
  margin-right: 16px;
  display: inline-block;
  vertical-align: top;
  width: 14px;
  height: 14px;
  background: url("../img/icon-heart.svg") no-repeat center;
}

.gallery__post-likes-count {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
  line-height: 2;
  color: #d22856;
  text-transform: uppercase;
}

.gallery__slogan {
  padding-top: 20px;
  padding-bottom: 42px;
  text-align: center;
  background-color: #ffffff;
}

.gallery__slogan-text {
  @include container();
  padding: 0 10px;
  font-size: 14px;
  line-height: 30px;
  letter-spacing: 0.3px;
}

@media #{$tablet} {
  .gallery__title {
    padding-top: 92px;
    padding-bottom: 76px;
    width: 450px;
    font-size: 18px;
    line-height: 1.67;
  }

  .gallery__title-text {
    display: inline-block;
    vertical-align: top;
  }

  .gallery__post-daily {
    margin-bottom: 50px;
  }

  .gallery__photo-daily {
    height: 509px;

    &::before {
      right: 3%;
      top: 11%;
    }
  }

  .gallery__photo {
    height: 300px;
    margin-bottom: 0;
  }

  .gallery__post-wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .gallery__post {
    width: 300px;
  }

  .gallery__post-desc {
    min-height: 155px;
    padding: 24px 31px 22px 27px;

    &--daily {
      padding: 29px 28px 15px;
      min-height: 120px;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;

      .gallery__post-text {
        order: 1;
      }

      .gallery__post-like {
        margin-left: 17px;
      }
    }
  }

  .gallery__post-likes {
    margin-top: auto;
    justify-content: space-between;
  }

  .gallery__post-like {
    margin-right: 0;
    order: 1;
  }

  .gallery__slogan {
    padding-top: 45px;
    padding-bottom: 69px;
  }

  .gallery__slogan-text {
    font-size: 18px;
    line-height: 1.67;
  }
}

@media #{$desktop} {
  .gallery__title {
    padding-bottom: 87px;
    width: 580px;
  }

  .gallery__photo-daily {
    height: 506px;

    &::before {
      top: 10.5%;
      right: 11%;
    }
  }

  .gallery__post {
    margin-bottom: 54px;
  }

  .gallery__photo {
    height: 400px;
  }

  .gallery__post-desc {
    &--daily {
      min-height: auto;
      padding: 30px 30px 13px 28px;
    }
  }

  .gallery__post-title {
    margin-bottom: 1px;
  }

  .gallery__post-text {
    letter-spacing: 0.28px;
  }

  .gallery__slogan {
    padding-top: 5px;
    padding-bottom: 66px;
  }
}
